import React from 'react';
import {
  Button,
  Form,
  Grid,
  Header,
  Message,
  Segment,
} from 'semantic-ui-react';

export default () => (
  <Grid centered columns={2}>
    <Grid.Column>
      <Header as="h2" textAlign="center">
        Logowanie do MeetMeAnyware
      </Header>
      <Segment>
        <Form size="large">
          <Form.Input
            fluid
            icon="user"
            iconPosition="left"
            placeholder="Login"
          />
          <Form.Input
            fluid
            icon="lock"
            iconPosition="left"
            placeholder="Hasło"
            type="password"
          />

          <Button onClick={() => alert('Zalogowano')}>Zaloguj</Button>
        
        </Form>
      </Segment>
      <Message>
        Nie masz jeszcze konta? <a href="https://matmark.github.io/MeetMeAnyware/">Zarejestruj się</a>
      </Message>
    </Grid.Column>
  </Grid>
);